@import "../../Containers/Variables.scss";

.page-slider {
  /* background: yellow; */
  height: 100vh;
  z-index: -1000;
  // margin-bottom: 10vh;
}

.page-slider-container {
  position: relative;
  margin-bottom: 10px;
  box-shadow: 0 3px 6px rgba(#000, 0.54);
}

@media (orientation: portrait) and (max-width: $phone-breakpoint) {
  .page-slider {
    height: 300px;
  }
}
@media (orientation: portrait) and (min-width: $phone-breakpoint) and (max-width: $tablet-breakpoint) {
  .page-slider {
    height: 600px;
  }
}

.page-slider-slide {
  position: absolute;
  // top: 100px;
  left: 0px;
  height: 100%;
  width: 100%;
}
