@import '../../../Containers/Variables.scss';

.slide-quote {
  position: absolute;
  // margin: 0 auto;
  bottom: 8%;
  right: 20%;
  // left: 0;
  z-index: 2000;
  color: #fff;

  h3 {
    text-align: right;
    font-size: 20px;
    text-shadow: 0 3px 10px #333;
  }
  
  p {
    font-size: 32px;
    text-shadow: 0 5px 10px #333;
  }

  @media (max-width: $phone-breakpoint) {
    h3 {
      font-size: 12px;
    }

    p {
      font-size: 18px;
    }
  }
}