@import '../../Containers/Helpers.scss';
@import '../../Containers/Variables.scss';

.page-videos-wrapper {
  background: #151515;
  padding-top: 40px;
}

.page-videos {
  display: flex;
  position: relative;
  padding-bottom: 20px;
  .video {
    width: 50%;
  }
}

.page-videos-button {
  // background: $background-color;
  border-radius: 6px;
  // height: 40px;
  display: flex;
  align-items: center;
  a {
    display: block;
    height: 100%;
    width: 100%;
    color: $background-color;
    text-decoration: none;
    font-size: 20px;
    padding-bottom: 20px;
    text-align: right;
    transition: 0.4s;
    &:hover {
      color: #fff;
    }
  }

}

@media (max-width: $container-width) {
  .page-videos {
    display: block;
    .video {
      width: 100%;
    }
  }
}
